
<template>
	<Carousel v-model="value1" loop>
		<CarouselItem v-for="(item ,index) in list" :key="index">
			<div class="demo-carousel">
				<img :src="item.url" />
			</div>
		</CarouselItem>
	</Carousel>
</template>
<script>
	export default {
		data() {
			return {
				list: []
			};
		},

		created() {
			this.getList();
		},
		methods: {
			getList() {
				this.$axios
					.get("http://gank.io/api/random/data/福利/10")
					.then(this.redeList);
			},
			redeList({ data: { results } }) {
				if (results instanceof Array) {
					this.list = results.map(item => ({
						name: item.who,
						url: item.url
					}));
					console.log(this.list);
				}
			}
		}
	};
</script>